<template>
<el-form ref="form" :model="form" :rules="formRules" label-width="90px" @submit.prevent="onSubmit" style="margin:20px;width:100%;min-width:600px;">
  <el-form-item label="网站主题" prop="topic">
    <el-input type="input" v-model="form.topic" class="sitetitle" placeholder="请输入网站主题"></el-input>
  </el-form-item>
  <el-form-item label="底二维码" >
  <div  v-model="form.qr_code">
    <el-upload class="avatar-erweima-uploader erweima" :file-list="qr_codeList"  list-type="picture"
      action="http://94.191.23.153:8888/api/image/upload"
      :on-remove="handleRemove1"
      :on-success="handleAvatarSuccess1"
      :before-upload="beforeAvatarUpload">
      <img v-if="files" :src="qr_code" class="erweima">
      <i v-else class="el-icon-plus avatar-uploader-icon"></i>
    </el-upload></div>
  </el-form-item>
  <div class="pictext">200*200的jpg/jpeg/png/gif</div>
  <br />
  <el-form-item label="联系方式" prop="contact">
    <el-input type="textarea" v-model="form.contact" class="sitecont" placeholder="请输入公司地址/QQ／客服电话"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click.native="editSubmit" :loading="editLoading">确认修改</el-button>
  </el-form-item>
</el-form>
</template>

<script>
import { getMessageList, editMessage, deleteImage } from '../../api/api';
export default {
  data() {
    return {
      files:'',
      form: {
        topic: '',
        qr_code:'',
        contact: '',
      },
      qr_codeList: [],
      editLoading: false,
      imageUrl: '',
      formRules: {
        topic: [{
          required: true,
          message: '请上传网站主题',
          trigger: 'blur'
        }],
        contact: [{
          required: true,
          message: '请输公司信息',
          trigger: 'blur'
        }],
      },}
  },
  methods: {
    initMessage () {
      getMessageList ().then((res) => {
        if (res.data.code === 0) {
          this.qr_codeList.push({
            url: 'http://94.191.23.153/images/' + res.data.data.qr_code
          })
          this.form = Object.assign({}, res.data.data);
        }
      })
    },
    //编辑
    editSubmit: function() {
      if (!this.form.qr_code) {
        this.$message({
          message: '请上传二维码！',
          type: 'warning'
        });
        return false;
      }
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.$confirm('确认提交吗？', '提示', {}).then(() => {
            this.editLoading = true;
            editMessage(this.form).then((res) => {
              this.editLoading = false;
              this.$message({
                message: '提交成功',
                type: 'success'
              });
              this.$refs['form'].resetFields();
              this.formVisible = false;
              this.initMessage();
            });
          });
        }
      });
    },
    handleRemove1(fileList) {
      this.form.qr_code = ''
      deleteImage({
        image_name: fileList.url.split('/')[fileList.url.split('/').length - 1]
      }).then((res) => {
        this.$message({
          message: '删除成功',
          type: 'success'
        });
      })
    },
    handleAvatarSuccess1(file, fileList) {
      this.form.qr_code = file.data.image_name[0]
      this.imageUrl = URL.createObjectURL(fileList.raw);
    },
    beforeAvatarUpload(files) {
      const isJPG = files.type === 'image/jpg';
      const isJPEG = files.type === 'image/jpeg';
      const isPNG = files.type === 'image/png';
      const isGIF = files.type === 'image/gif';
      const isLt2M = files.size / 1024 / 1024 < 2;
      if (!isJPG && !isJPEG && !isPNG && !isGIF && !isGIF) {
        this.$message.error('上传图片格式只能是 JPG/JPEG/PNG/GIF 格式!');
      }
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 2MB!');
      }
      return isJPG || isJPEG || isPNG || isGIF && isLt2M;
    }
  },
  mounted () {
    this.initMessage()
  }
}
</script>
<style  scoped>
  .avatar-uploader {
    display: inline-block;
    margin-right: 10px;
  }


  .avatar-erweima-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 100px;
    height: 100px;
    line-height: 100px;
    text-align: center;
  }


  .avatar-erweima-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 100px;
    height: 100px;
    line-height: 100px;
    text-align: center;
  }

  .pictext {
      display: block;
      margin-top:-8px;
      font-size: 13px;
      color: #8c939d;
      width: 200px;
      height:25px;
      margin-bottom:15px;
      margin-left: 90px;
  }

  .erweima img {
    width: 100px;
    height: 100px;
  }


  .upload-pic {
    display: inline-table;
  }

  .sitetitle,
  .sitecont {
    width: 80%;
  }


  .avatar-uploader-icon {
    font-size: 32px;
    color: #8c939d;
    width: 100px;
    height: 100px;
    line-height: 100px;
    text-align: center;
    border: 1px solid #c0ccda;
    border-radius: 6px;
    box-sizing: border-box;
  }
</style>



