import route from '../routes.js'
import {
  Message
} from 'element-ui' //引入信息
import http from 'axios';

//前后端交互引入头部设置文件
let axios = http.create({
  baseURL: 'http://94.191.23.153:8888',
  withCredentials: true,
  headers: {
    'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
  },
  transformRequest: [function(data) {
    let newData = '';
    for (let k in data) {
      if (data.hasOwnProperty(k) === true) {
        newData += encodeURIComponent(k) + '=' + encodeURIComponent(data[k]) + '&';
      }
    }
    return newData;
  }]
});

//加入登陆拦截器
axios.interceptors.request.use(config => {
  if (config.url.indexOf('/login') >= 0) {
    sessionStorage.setItem('userToken', 'applicationToken')
    return config;
  } else if (sessionStorage.getItem('userToken') === 'applicationToken') {
    return config;
  } else {
    route.push({
      path: '/login'
    });
    sessionStorage.removeItem('userToken');
    sessionStorage.removeItem('userId');
  }
}, err => {
  return Promise.reject(err);
});

axios.interceptors.response.use(function(response) {
    // 判断是否是admin主账号登录  主账号显示账号管理，其他账号不显示 
    if (response.data.code === 0 && response.data.data && response.data.data.user_id && response.data.data.user_id !== 1) {
        route.options.routes.map((item) => {
            if (item.name === '添加权限') {
                item.hidden = true
            }
        })
    }
    return response;
}, function(error) {
  if (error.response.status === 401) {
    route.push({
      path: '/login'
    });
    sessionStorage.removeItem('userToken');
    sessionStorage.removeItem('userId');
    Message.error({
      message: error.response.data.msg
    })
  }
  return Promise.reject(error);
});

import Header from './header' //前后端交互引入头部设置文件
